import { Component, OnInit } from '@angular/core';
import { WheatherService } from './weather.service';
@Component({
    selector: 'app-weather',
    templateUrl: './weather.component.html',
    styleUrls: ['./weather.component.css'],
    providers: [WheatherService]
})
export class WeatherComponent implements OnInit {
    txtCityName = '';
    cityName = '';
    temp = null;
    loading = false;

    constructor(private weatherService: WheatherService) { }

    ngOnInit() {  
    }
    getWeather() {
        this.loading = true;
        this.weatherService.getTemp(this.txtCityName)
            .then(temp => {
                this.cityName = this.txtCityName;
                this.temp = temp;
                this.loading = false;
                this.txtCityName = '';
            })
            .catch(err => {
                alert('Cannot find your city!');
                this.loading = false;
                this.cityName = '';
                this.txtCityName = '';
            });
    }
    getMessage() {
        if (this.loading) {
            return 'Loading....';
        }
       return this.cityName === '' ? 'Enter your city name' : (this.cityName + ' is now ' + this.temp)
    }
}
